.app{
    height: 100vh;
    .sections{
        width:100%;
        height: calc(100vh - 70px);
        background-color: lightsalmon;
        position: relative;
        top:70px;
        scroll-behavior: smooth;
        scroll-snap-type:y mandatory;
        // its for firefox
        scrollbar-width: none;
        &::-webkit-scrollbar{
            display:none;
        }
        > *{
            width:100vw;
            height:calc(100vh - 70px);
        }
    }
}